.container {
  flex-direction: column;
  width: 520px;
  height: auto;
  margin: 150px auto 0;
  padding: 25px;
  font-family: Bungee Inline, sans-serif;
  display: flex;
}

.wrapper {
  justify-content: space-between;
  margin-bottom: 20px;
  display: flex;
}

form {
  width: 250px;
}

label > input {
  color: initial;
  border: 1px solid #000;
  width: 100%;
  height: 32px;
  font-size: 18px;
}

input {
  color: #ffb866;
  text-align: center;
  background-color: #fff;
  border: 3px solid #ffb866;
  width: calc(100% - 156px);
  font-family: inherit;
  font-size: 21px;
}

form > button {
  color: initial;
  text-transform: lowercase;
  border: 1px solid #000;
  width: 100%;
  margin-top: 15px;
  padding: 4px 0;
  font-size: 22px;
}

button {
  color: #ffb866;
  background-color: #fff;
  border: 3px solid #ffb866;
  padding: 5px 25px;
  font-family: inherit;
  font-size: 24px;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
}

button.isActive {
  color: #fff;
  background-color: #ffb866;
}

.timer {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.field {
  border-radius: 13% 13% 0 0;
  flex-direction: column;
  min-width: 97px;
  display: flex;
  overflow: hidden;
}

.field:first-child {
  min-width: 135px;
}

.value {
  color: #fff;
  text-align: center;
  background-color: #ffb866;
  margin-bottom: 5px;
  font-size: 50px;
  line-height: 1.3;
  display: inline-block;
}

.label {
  color: #ffb866;
  text-align: center;
  background-color: #fff;
  border: 3px solid #ffb866;
  border-top-style: dashed;
  justify-content: center;
  font-size: 24px;
  line-height: 1.35;
  display: inline-flex;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif, Bungee Inline, cursive;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

input {
  padding: 0;
}

input:focus-visible {
  border: none;
}

